/* index.css */
body {
  margin: 0;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  background-color: #111;
  color: #fff;
  overflow-x: hidden;
}

/* Splash screen */
.splash-screen {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: black;
}

.netflix-logo-pop {
  font-size: 5rem;
  font-weight: bold;
  color: #e50914;
  animation: pop 1.5s ease-out forwards;
}

@keyframes pop {
  0% {
    transform: scale(0.1);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

/* Login screen */
.login-screen {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.9), transparent), url('https://assets.nflxext.com/ffe/siteui/vlv3/81f3c1cc-e007-4c7b-a05d-d1d9c1eb7c6c/fbbc4ea1-7a34-43cb-83a6-1e716ad0f3d7/IN-en-20230410-popsignuptwoweeks-perspective_alpha_website_medium.jpg') center/cover;
}

.login-box {
  background: rgba(0, 0, 0, 0.75);
  padding: 2rem;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 300px;
}

.login-box input {
  padding: 0.7rem;
  border: none;
  border-radius: 4px;
}

.login-box button {
  background: #e50914;
  color: white;
  border: none;
  padding: 0.7rem;
  border-radius: 4px;
  font-weight: bold;
  cursor: pointer;
}

/* Home screen layout */
.home-screen {
  display: flex;
}

.sidebar {
  width: 60px;
  background: #000;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 1rem;
  gap: 1.5rem;
  font-size: 1.5rem;
  border-right: 1px solid #333;
}

.content {
  flex: 1;
  padding: 1rem;
}

.hero-banner {
  position: relative;
  width: 100%;
  height: 60vh;
  overflow: hidden;
  border-radius: 20px;
  margin-bottom: 1.5rem;
}

.hero-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: brightness(60%);
}

.hero-content {
  position: absolute;
  top: 30%;
  left: 3rem;
  color: white;
}

.hero-buttons button {
  margin-right: 1rem;
}


.play {
  background: white;
  color: black;
}

.trailer {
  background: rgba(109, 109, 110, 0.7);
  color: white;
}

/* Movie rows */
.movie-row {
  margin-top: 2rem;
}

.movie-row h3 {
  margin-left: 0.5rem;
  font-size: 1.3rem;
}

.poster-container {
  display: flex;
  overflow-x: auto;
  padding: 0.5rem;
  gap: 0.8rem;
}

.poster-container img {
  width: 150px;
  height: 220px;
  border-radius: 12px;
  object-fit: cover;
  transition: transform 0.3s;
  cursor: pointer;
}

.poster-container img:hover {
  transform: scale(1.08);
}
